import styled from 'styled-components';

export const HomePageContainer = styled.div`
    width: 100%;
    height: 100%;
    max-width: 900px;
    align-self: center;
    transition: 0.4s;
    position: fixed;
    top: ${({pos}) => 
                pos === 'show' ? '-20%': '0%'};

    .main {
        width: 100%;
        height: 100%;

        .menu {
            font-weight: bold;
            height: 35px;
            width: 35px;
            text-align: center;
            position: absolute;
            top: 2%;
            // right: ${window.screen.width <= 1000 ? '5%' : '5%'};
            right: 4%;
            border-radius: 50%;
            background-color: rgba(255, 255, 255, 0.5);
        }
        
        .add {
            font-size: 30px;
            text-shadow: white 0px 0px 10px;
            width: 100%;
            max-width: 900px;
            position: absolute;
            text-align: center;
            bottom: 2%;

            span {
                backdrop-filter: blur(100%);
	            -webkit-backdrop-filter: blur(100%);

                &:hover {
                    cursor: pointer;
                }
            }

            
        
        }
    }

    .footer {
        color: white;
        background-color: black;
        width: 100%;
        height: 20%;

        .foot-box {
            font-size: 0.7em;
            width: 100%;
            height: 100%;
            // text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
`;