import styled from 'styled-components';

export const FullHomeBox = styled.div`
    height: 100%;
    width: 100%;
    // position: absolute;
    // top: 0;
    // left: 0;
    overflow: scroll;
`;

export const HeaderBox = styled.div`
    width: 100%;
    position: relative;
    
    .cover-box {
        width: 100%;
        height: ${window.screen.width <= 1000 ? '55%' : '25%'};
        // max-width: 900px;
        overflow: hidden;
        transition: 0.8s;
        position: absolute;
        top: ${({pos}) => 
            pos === 'show' ? '0%': '-55%'}; 
        left: 0%;
        z-index: -1;

        img {
            width: 100%;
            height: 140%;
            padding: 0;
            margin: 0;
            position: relative;
            top: -9%;           
        }
    }

    .pic-box {
        // padding-top: 15%;
        padding-top: ${({pos}) => 
        pos === 'show' ? '15%': '5%'}; 
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;

        img {
            background-color: white;
            border: 4px solid white;
            margin-bottom: 4%;
            width: ${({pos}) => 
            pos === 'show' ? '80px': '105px'}; 
            height: auto;
            border-radius: 50%;
            transition: 0.7s;
            position: relative;
            top: ${({pos}) => 
                pos === 'show' ? '100%': '0%'};
            left: ${({pos}) => 
                pos === 'show' ? '-35%': '0%'};
            /* margin-top: 5%; */ 
        }
    }
`;

export const Overview = styled.div`
    padding-left: ${window.screen.width <= 1000 ? '2.2%' : '15%'};
   { margin-top: ${
        window.screen.width <= 500 
        ? '44%' 
        : window.screen.width <= 1000
        ? '33%' : '22%'
    };}
    // background-color: green;
    flex-direction: column;
    // text-align: center;

    h3 {
        margin: 0;
        padding: 0;
    }

    .username {
        font-size: 15px;
    }

    .info {
        margin-top: 5px;
    }
`;

export const Menu = styled.div`
    font-weight: bold;
    padding: 3%;
    padding-left:  ${window.screen.width <= 1000 ? '2.2%' : '14.4%'};
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    

    span {
        padding: 0% 7% 0% 3%;
        
    }
    
    .about {
        border-bottom: ${({picked}) => 
            picked === 0 ? '3px solid': null};
    }

    .reviews {
        border-bottom: ${({picked}) => 
            picked === 1 ? '3px solid': null};
    }

    .blogs {
        border-bottom: ${({picked}) => 
            picked === 2 ? '3px solid': null};
    }
`;


export const MainBox = styled.div`
    // height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50%;

    img {
        width: 80%;
        max-width: 400px;
        padding-bottom: 10px;
    }

    p {
        width: 95%;
        margin: 0;
        padding-bottom: 10px;
    }
`;