export const CardsBox = styled.div`
    // background-color: red;
    // padding-top: 2%;
    width: 100%;
    height: auto;
    display: flex;
    flex: 1 1 auto;
    justify-content: center;
    align-items: center;
    overflow: auto;
    
    .cardsbox {
        width: 450px;
        max-width: 70%;
        padding: 5px;

        b {
            font-size: ${window.screen.width <= 500 ? '15px' : '20px'};
            margin: 0;
            padding: 0;
        }
    }
`;