import { useState } from 'react';

import MiniHomePage from '../miniHomePage';
import FullHomePage from '../fullHomePage';

import { HomePageContainer } from './homePage.styles';


const HomePage = () => {
  const [ miniFull, setMiniFull ] = useState('+');
  const [ covered, setCovered ] = useState('no-show');
  const [ page, setPage ] = useState(<MiniHomePage />);

  const showFooter = () => {
    if (covered === 'no-show') {
      setCovered('show')
      setMiniFull('x')
    } else{
      setCovered('no-show')
      setMiniFull('+')
    }
  }

  return (
    <HomePageContainer pos={covered}>
      <div className='main'>
        <div className="menu">...</div>
        <MiniHomePage />
        <div className="add" >
          <span onClick={showFooter}>
            {miniFull}
          </span>
        </div>
      </div>
      <div className='footer'>
        <div className='foot-box'>
          Copyright @ Tree Inc 2024
        </div>
      </div>
    </HomePageContainer>
  );
}

  export default HomePage;